.contacts {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;

    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #9FADC2;
  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #3E3E3E;
    text-decoration: none;

    display: inline-block;
    border-bottom: 1px solid transparent;
    transition: all 0.3s;

    &:hover {
      color: #000;
      border-color: #3E3E3E;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}